import React, {useState, useEffect} from 'react';
import Articles from './Article-content';
import ArticleList from '../components/ArticlesList';
import NotFoundPAge from './notFoundPage';
import Comments from '../components/CommentsList';
import UpvoteSection from '../components/UpvoteSection';
import AddCommentForm from '../components/AddCommentForm';

const ArticlePage = ({match}) => {
    const name = match.params.name;
    const information = Articles.find(e=>e.name === name);
    const otherArticles = Articles.filter(e=>e.name !== name);
    const [articleInfo, setArticleInfo] = useState({upvotes:0,comments:[]});
    useEffect(()=>{
        const fetchData = async () =>{
            const result = await fetch(`/api/articles/${name}`)
            const body = await result.json();
            setArticleInfo(body);
        }
        fetchData();
    },[name]);

    if (!information) return <NotFoundPAge/>
    return (
    <React.Fragment>
        <h1>{information.title}</h1>
        <UpvoteSection articleInfo={articleInfo} setArticleInfo={setArticleInfo}/>
        {information.content.map((paragraph,key) => <p key={key}>{paragraph}</p>)}
        <AddCommentForm articleInfo={articleInfo} setArticleInfo={setArticleInfo}/>
        <Comments comments={articleInfo.comments}/>
        <h3>Other Articles</h3>
        <ArticleList articles={otherArticles}></ArticleList>
    </React.Fragment>
)
}

export default ArticlePage;