import React from 'react';



const UpvoteSection = ({articleInfo, setArticleInfo}) => {

    const upvoteArticle = async () => {
        const result = await fetch(`/api/articles/${articleInfo.name}/upvotes`,{
            method: 'POST'
        })
        const body = await result.json();
        setArticleInfo(body);
    }

    return (
        <div id="upvotes-section">
            <button onClick={()=>upvoteArticle()}>Add Upvote</button>
            <p>This post has been voted {articleInfo.upvotes} times</p>
        </div>)

};

export default UpvoteSection;