import React, {useState} from 'react';

const AddCommentForm = ({articleInfo,setArticleInfo}) => {
    
    const [username,setUsername] = useState('');
    const [commentText, setCommentText] = useState('');

    const updateComments = async () => {
        if (!username || !commentText) return;
        const result = await fetch(`/api/articles/${articleInfo.name}/add-comment`,{
            method: 'POST',
            body : JSON.stringify({username,
                'text':commentText}),
            headers: {
                'Content-Type':'application/json',
            }
        }) 

        const body = await result.json();
        setArticleInfo(body);
        setUsername("");
        setCommentText("")
    }
    
    
    return (

    <div id="add-comment-form">
        <h3>Add a comment</h3>
        <label>
            Name:
            <input type="text" val={username} onChange={(event)=>setUsername(event.target.value)}/>
        </label>
        <label>
            Comment:
            <textarea rows="4" cols="50" val={commentText} onChange={event =>setCommentText(event.target.value)}/>
        </label>
        <button onClick ={()=>updateComments()}>Add Comment</button>

    </div>
)};


export default AddCommentForm;